export default {
	data() {
		return {
			oldPsd:'',
			newPsd:'',
			newPsd1:'',
			id:'',
			token:'',
		}
	},
	onShow(){
		this.token = uni.getStorageSync('token')
		console.log(this.token)
		uni.request({
			url: this.url + '/mobile/realData',
			method:'GET',
			header: {
				'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				'token': this.token
			},
			data:{
				
			},
			success: (res) => {
				console.log(res);
				this.id = res.data.userId
			}
		})
	},
	methods: {
		updatePsd(){
			if(this.newPsd != this.newPsd1){
				uni.showModal({
				    title: '提示',
				    content: '输入的两次新密码不一致',
				});
				return false
			}
			uni.request({
				url: this.url + '/mobile/updatePwd',
				method:'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
					'token': this.token
				},
				data:{
					id:this.id,
					oldpwd:this.oldPsd,
					newpwd:this.newPsd
				},
				success: (res) => {
					console.log(res);
					if(res.data.result){
						uni.showToast({
							title: '修改成功',
							duration: 2000,
							success:function(){
								uni.navigateBack({
									delta: 1
								});
							}
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon:'none',
							duration: 2000
						});
					}
				}
			})
		}
	}
}
